@use "../mixins";
@use "../variables";

.card {
  width: 100%;

  @media (max-width: variables.$mobile-width-only) {
    margin-top: 24px;
  }
}

.card:first-child {
  @media (min-width: variables.$tablet-width) {
    grid-column: 1/3;
  }

  @media (min-width: variables.$desktop-width) {
    grid-column: 1/4;
  }
}

.card:not(:last-child) {
  @media (max-width: variables.$mobile-width-only) {
    border-bottom: 1px solid #E5E5E5;
  }
}

.card__image {
  display: block;
  object-fit: cover;

  @media (max-width: variables.$mobile-width-only) {
    margin: 0 20px 15px 20px;
    width: calc(100% - 40px);
    min-height: 280px;
  }

  @media (min-width: variables.$tablet-width) {
    width: 100%;
    min-height: 400px;
  }

  @media (min-width: variables.$desktop-width) {
    width: 100%;
    min-height: 400px;
  }
}

.card__details {
  margin: 0 20px;
  width: calc(100% - 40px);

  @media (min-width: variables.$tablet-width) {
    margin: 0;
    padding: 22px 20px 15px 20px;
    width: 100%;
    background-color: #F2F2F2;
    display: flex;
    justify-content: space-between;
  }

  @media (min-width: variables.$desktop-width) {
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    position: absolute;
    min-height: 80px;
    top: calc(100% - 89px);
  }
}

.card__text-wrapper {
  @media (max-width: variables.$mobile-width-only) {
    margin-bottom: 11px;
  }
}

.card__title {
  @include mixins.reset-header-paragraph;
  text-transform: uppercase;

  @include mixins.text--bold(14px, variables.$text-color-primary);

  @media (min-width: variables.$desktop-width) {
    @include mixins.text--bold(14px, variables.$text-color-alternate);
  }
}

.card__description {
  display: block;

  @include mixins.text--normal(14px, variables.$text-color-secondary);

  @media (min-width: variables.$desktop-width) {
    @include mixins.text--bold(14px, variables.$text-color-alternate);
  }
}

.card__like-wrapper {
  @media (max-width: variables.$mobile-width-only) {
    margin-bottom: 25px;
  }

  @media (min-width: variables.$tablet-width) {
    align-self: center;
  }
}

.card__like-image {
  margin-right: 14px;
}

.card__like-counter {
  @include mixins.text--bold(14px, variables.$text-color-primary);

  @media (min-width: variables.$desktop-width) {
    @include mixins.text--bold(14px, variables.$text-color-alternate);
  }
}

.card__like-icon > path {
  color: variables.$text-color-primary;
  opacity: 0.3;

  @media (min-width: variables.$desktop-width) {
    color: variables.$text-color-alternate;
    opacity: 1;
  }
}
